<template>
  <el-button circle :size="size" :type="type" :icon="icon"
    :loading="loading" :disabled="!hasPerms(perms)" @click="handleClick">
    <!-- {{label}} -->
  </el-button>
</template>

<script>
import { hasPermission } from '@/permissions/index.js'
export default {
  name: 'ManageButton',
  props: {
    label: {
      type: String,
      default: 'Button'
    },
    size: {
      type: String,
      default: 'mini'
    },
    type: {
      type: String,
      default: null
    },
    loading: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    perms: {
      type: String,
      default: null
    },
    icon: {
      type: String,
      default: null
    }
  },
  data() {
    return {
    }
  },
  methods: {
    handleClick: function () {
       this.$emit('click', {})
    }, 
    hasPerms: function (perms) {
      if (perms != null) {
        // console.log(perms)
        return hasPermission(perms)
      }
      return true;
    }
  },
  mounted() {
  }
}
</script>

<style scoped>

</style>


